<template>
  <div class="index-detail">
    <div v-if="info">
      <h3>{{ info.title }}</h3>
      <p>{{ info.desc }}</p>
      <img
        class="index-detail-img"
        v-for="(img, index) in info.imgUrls"
        :key="'img' + index"
        :src="staticURL + img"
        alt="无法显示"
      />
    </div>
    <div v-else>加载中...</div>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted } from "vue";
import { useRoute } from "vue-router";
import { staticURL } from "../api/config.js";
import { getProjectById } from "../api/wxProject.js";
export default {
  name: "index",
  components: {},
  setup() {
    let route = useRoute();
    let query = route.query;
    const state = reactive({
      info: null,
      staticURL: staticURL,
    });

    onMounted(() => {
      init();
    });

    const init = async () => {
      let id = query.id;
      getProjectById(
        {},
        (res) => {
          // console.log(res);
        },
        (res) => {
          state.info = res;
        },
        {
          method: "get",
          params: {
            id,
          },
        }
      );
    };
    const methods = {};

    return {
      ...toRefs(state),
      ...methods,
    };
  },
};
</script>
<style>
.index-detail-img {
  width: 100%;
}
</style>
